import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Global } from '../models/global';
import { Kpir, KpirFilter } from '../models/models';

@Injectable({
  providedIn: 'root'
})
export class KpirService {
  private url: string;
  public kpirId: number;

  constructor(private http: HttpClient) {
    this.url = Global.url;
  }

  getAll(filter: KpirFilter) {
    return this.http.post<Kpir[]>(this.url + `/kpir`, filter);
  }

  getOne(id: number) {
    return this.http.get<Kpir>(this.url + `/kpir/` + id);
  }

  create(kpir: Kpir) {
    return this.http.post<Boolean>(this.url + `/kpir/`, kpir);
  }

  update(kpir: Kpir) {
    return this.http.put<Boolean>(this.url + `/kpir/`, kpir);
  }

  delete(id: number) {
    return this.http.delete<Boolean>(this.url + `/kpir/` + id);
  }

  getNextIdx() {
    return this.http.get<Number>(this.url + "/kpir/idx")
  }
}
