import { Injectable, OnDestroy } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Global } from '../models/global';
import { map } from 'rxjs/operators';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class AuthenticationService implements OnDestroy{
  url: string;
  constructor(private http: HttpClient, private router: Router) {
    this.url = Global.url;
  }
  login(username: string, password: string) {
    let value: string = window.btoa(username + ':' + password);

    // let options = { options: { headers: headers } };
    return this.http.post<Boolean>(this.url + `/user/login`, { email: username, password: password})
      .pipe(map(user => {
        if (user) {
          let authdata = window.btoa(username + ':' + password);
          sessionStorage.setItem('currentUser', JSON.stringify(authdata));
        }
        return user;
      }));
  }

  logout() {
    // sessionStorage.removeItem('currentUser');
    sessionStorage.clear()
    this.router.navigateByUrl("/login");
  }

  isLogged() {
    let logged = sessionStorage.getItem("currentUser");
    return logged;
  }

  ngOnDestroy(): void {
    sessionStorage.clear()
  }
}
