import { Injectable } from '@angular/core';
import { Global } from '../models/global';
import { KpirFilter } from '../models/models';
import { HttpClient } from '@angular/common/http';


@Injectable({
  providedIn: 'root'
})
export class PrinterService {
  private url: string;

  constructor(private http: HttpClient) {
    this.url = Global.url;
  }

  getKpirAllResponse(filter: KpirFilter) {
    return this.http.post<Response>(this.url + "/printer/kpirs", filter);
  }

  getKpirAllBlob(filter: KpirFilter) {
    console.log(filter)
    const httpOptions = {
      'responseType': 'blob' as 'json'
    };
    return this.http.post<any>(this.url + "/printer/kpir", filter, httpOptions);
  }

  sendKpirAllViaMail(filter: KpirFilter) {
    return this.http.post<void>(this.url + "/printer/mail/kpir", filter);
  }
  
}