import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Global } from '../models/global';
import { User, UserRegister, ActivateModel } from '../models/models';

@Injectable({
  providedIn: 'root'
})
export class UserService {
  url: string;
  constructor(private http: HttpClient) {
    this.url = Global.url;
  }

  get() {
    return this.http.get<User>(this.url + `/user/`);
  }

  registration(user: UserRegister) {
    return this.http.post<Boolean>(this.url + `/user/registration`, user);
  }

  update(user: User) {
    return this.http.put<Boolean>(this.url + `/user/`, user);
  }

  authorize(activateModel: ActivateModel) {
    return this.http.post<Boolean>(this.url + `/user/authorize`, activateModel);
  }

  deleteAccount(id: number) {
    return this.http.delete<Boolean>(this.url + `/account/` + id);
  }

  getRegistrationDate() {
    return this.http.get<string>(this.url + `/user/registration/date`);
  }


}
