import { Injectable } from '@angular/core';
import { Global } from '../models/global';
import { HttpClient } from '@angular/common/http';
import { ContractorMini, Contractor, ContractorFilter, ContractorSelect } from '../models/models';

@Injectable({
  providedIn: 'root'
})
export class ContractorService {
  private url: string;
  constructor(private http: HttpClient) {
    this.url = Global.url;
  }

  getAll(filter: ContractorFilter) {
    return this.http.post<ContractorMini[]>(this.url + `/contractor`, filter);
  }

  getOne(id: number) {
    return this.http.get<Contractor>(this.url + `/contractor/` + id);
  }

  create(contractor: Contractor) {
    return this.http.post<Boolean>(this.url + `/contractor/`, contractor);
  }

  update(contractor: Contractor) {
    return this.http.put<Boolean>(this.url + `/contractor/`, contractor);
  }

  delete(id: number) {
    return this.http.delete<Boolean>(this.url + `/contractor/` + id);
  }

  getAllSelect() {
    return this.http.get<ContractorSelect[]>(this.url + `/contractor`);
  }
}
